import axios from "axios";
import "svelte/internal";
import { writable } from "svelte/store";
import { words, bigrams, trigrams } from "../words";
class GameMode {
    constructor() {
        this.filteredBigrams = bigrams.filter((bigram) => bigram[1] > 1000);
        this.filteredTrigrams = trigrams.filter((trigram) => trigram[1] > 1000);
        this.ngrams = [...this.filteredBigrams, ...this.filteredTrigrams];
        this.isStopped = true;
        this.baseState = {
            currentNgram: null,
            currentWord: "",
            usedLetters: new Set(),
            usedWords: [],
            score: 0,
            gradientPercentage: 100,
            wordMeanings: null,
        };
        this.ui = writable(this.baseState);
        // Settings for the specific game mode
        this.settings = {};
        this.updateTimeout = null;
        this.definitionTimeout = null;
    }
    pickNewNgram() {
        return this.ngrams[Math.floor(Math.random() * this.ngrams.length)][0];
    }
    reset() {
        this.ui.set(Object.assign(Object.assign({}, this.baseState), { currentNgram: this.pickNewNgram() }));
        this.isStopped = true;
        clearTimeout(this.updateTimeout);
    }
    update() {
        const self = this;
        this.updateTimeout = setTimeout(() => {
            self.update();
        }, 1000);
    }
    startRound() {
        if (!this.isStopped)
            return;
        this.isStopped = false;
        this.onStart();
        this.update();
    }
    skipNgram() {
        if (this.isStopped)
            return;
        this.ui.update((ui) => {
            return Object.assign(Object.assign({}, ui), { currentNgram: this.pickNewNgram() });
        });
    }
    onStart() { }
    onSubmit(newState, oldState) { }
    getMeanings(word) {
        axios
            .get("https://api.dictionaryapi.dev/api/v2/entries/en/" + word)
            .then((res) => {
            console.log(res.data[0].meanings);
            this.ui.update((ui) => (Object.assign(Object.assign({}, ui), { wordMeanings: res.data[0].meanings })));
        });
    }
    submitWord() {
        let newState = null;
        let oldState = null;
        this.ui.update((ui) => {
            console.log(ui);
            oldState = ui;
            const currentWord = ui.currentWord.toLowerCase();
            if (!currentWord.includes(ui.currentNgram) ||
                !words.includes(currentWord) ||
                ui.usedWords.includes(currentWord)) {
                return ui;
            }
            this.getMeanings(ui.currentWord);
            newState = Object.assign(Object.assign({}, ui), { currentNgram: this.pickNewNgram(), currentWord: "", usedLetters: new Set([...ui.usedLetters, ...currentWord]), usedWords: [currentWord, ...ui.usedWords] });
            return newState;
        });
        // The state wasn't updated, validation didn't pass
        if (!newState)
            return;
        this.onSubmit(newState, oldState);
    }
}
export class Alphabet extends GameMode {
    constructor() {
        super(...arguments);
        this.roundStartTime = new Date().getTime();
    }
    checkWinningCondition(usedLetters) {
        if (usedLetters.size == 26)
            return true;
        return false;
    }
    update() {
        super.update();
        this.ui.update((ui) => (Object.assign(Object.assign({}, ui), { score: Math.floor((new Date().getTime() - this.roundStartTime) / 1000) })));
    }
    skipNgram() {
        if (this.isStopped)
            return;
        this.ui.update((ui) => {
            console.log("change currentWord to none");
            this.roundStartTime = this.roundStartTime - 5000;
            return Object.assign(Object.assign({}, ui), { currentWord: "", currentNgram: this.pickNewNgram(), score: Math.floor((new Date().getTime() - this.roundStartTime) / 1000) });
        });
    }
    onStart() {
        this.roundStartTime = new Date().getTime();
    }
    onSubmit(newState, _) {
        console.log(this.checkWinningCondition(newState.usedLetters));
    }
}
export class AlphabetLeastWords extends GameMode {
    onSubmit(newState, _) {
        this.ui.update((ui) => (Object.assign(Object.assign({}, ui), { score: ui.score + 1 })));
    }
}
export class HighScore extends GameMode {
    constructor() {
        super(...arguments);
        this.answerTime = 10;
        this.answerTimer = 0;
    }
    reset() {
        super.reset();
        this.answerTimer = this.answerTime;
    }
    update() {
        super.update();
        if (this.answerTimer < 1)
            return this.reset();
        this.answerTimer -= 2;
        this.ui.update((ui) => (Object.assign(Object.assign({}, ui), { gradientPercentage: Math.floor((this.answerTimer / this.answerTime) * 100) })));
    }
    onSubmit(newState, oldState) {
        // TODO: add better score calculation
        const score = oldState.currentWord.length * 2;
        this.answerTimer = Math.min(score + this.answerTimer, this.answerTime);
        this.ui.update((ui) => (Object.assign(Object.assign({}, ui), { score: ui.score + score })));
    }
}
